* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

p {
  margin: 0;
  margin-right: 10px;
  font-size: 14px;
}

.country-list {
  margin-top: 0;
  padding-left: 0;
}

.country-list li {
  display: flex;
  align-items: center;
}

.country-info {
  position: absolute;
  width: 250px;
  transition: transform 300ms linear;
}

.hidden {
  transform: translateX(-100%);
}

.country-info h1 {
  margin-top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.country-info p+p {
  margin-top: 5px;
}

.country-info p span {
  font-weight: 700;
}

